import React, { useState, useEffect } from "react";
import "antd/dist/antd.css";
import { Button, Table } from "antd";
import { CCard, CCardBody, CCardHeader, CCol, CRow } from "@coreui/react";
import { useSelector } from "react-redux";
import axios from "axios";
import { Link } from "react-router-dom";

const ProductSale = () => {
  const user = useSelector((state) => state.user);

  const [products, setProducts] = useState([]);

  useEffect(() => {
    //district_code 
    axios
      .get(
        `/v1/products?page=1&rows=10&store_id=${user.storeData.id}&district_code=1111054000`
      )
      .then((response) => {
        setProducts(response.data.data);
      });
  }, []);

  //
  const onStartHandler = (item) => {
    let body = {
      use_yn: "Y",
    };

    axios
      .put(`/v1/products/${item}`, body)
      .then((response) => console.log(" ", response));
  };

  //
  const onStopHandler = (item) => {
    console.log("zz", item);
    let body = {
      use_yn: "N",
    };

    axios
      .put(`/v1/products/${item}`, body)
      .then((response) => console.log(" ", response));
  };

  // antd 
  const columns = [
    { title: "NO.", dataIndex: "id", key: "id" },
    { title: "", dataIndex: "title", key: "title" },
    { title: "", dataIndex: "cost_price", key: "cost_price" },
    { title: "", dataIndex: "sale_price", key: "sale_price" },
    { title: "", dataIndex: "favorite_count", key: "favorite_count" },
    {
      title: "",
      dataIndex: "",
      key: "x",
      render: (record) => {
        return (
          <>
            <Link
              to={{
                pathname: "/product/productUpdate",
                state: {
                  record,
                },
              }}
            >
              <Button></Button>
            </Link>
          </>
        );
      },
    },
    {
      title: "",
      dataIndex: "",
      key: "x",
      render: (record) => {
        return (
          <>
            {products &&
              products.map((item) => {
                if (item.id !== record.id) {
                  return null;
                } else {
                  if (item.use_yn === "Y") {
                    return (
                      <Button danger onClick={() => onStopHandler(item.id)}>
                        
                      </Button>
                    );
                  } else {
                    return (
                      <Button
                        type="primary"
                        onClick={() => onStartHandler(item.id)}
                      >
                        
                      </Button>
                    );
                  }
                }
              })}
          </>
        );
      },
    },
  ];

  return (
    <CRow>
      <CCol xs="12">
        <CCard>
          <CCardHeader>
            <h1></h1>
          </CCardHeader>
          <CCardBody>
            <Table
              columns={columns}
              dataSource={products}
              scroll={{ x: 700 }}
            />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default ProductSale;
